import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { updateCursor } from "../projectSlice";
import icon from "../icon";
import {
    Navbar,
    Nav,
    NavItem,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem
} from "reactstrap";

export default function Toolbar() {
    const cursor = useSelector(state => state.project.cursor);
    const dispatch = useDispatch();

    const setCursor = type => () => dispatch(updateCursor(type));
    
    return (
        <Navbar className="toolbar">
            <Nav navbar>
                <NavItem onClick={setCursor("default")}><img src={icon.CursorDefaultLight} /></NavItem>
                <NavItem> 
                    <UncontrolledDropdown direction="end">
                        <DropdownToggle nav onClick={setCursor("crosshair")}>
                            <i className="bi bi-square" title="Shape tool"></i>
                        </DropdownToggle>
                        <DropdownMenu>
                            <DropdownItem><i className="bi bi-square" title="Rectangle"></i></DropdownItem>
                            <DropdownItem><i className="bi bi-circle" title="Circle"></i></DropdownItem>
                            <DropdownItem><i className="bi bi-square" title="Hexagon"></i></DropdownItem>
                            <DropdownItem><i className="bi bi-square" title="Trapeze"></i></DropdownItem>
                        </DropdownMenu>
                    </UncontrolledDropdown>
                </NavItem>
                <NavItem onClick={setCursor("grab")}><img src={icon.CursorHandLight} /></NavItem>
                <NavItem onClick={setCursor("zoom-in")}><img src={cursor === "zoom-out" ? icon.CursorZoomOutLight : icon.CursorZoomInLight} /></NavItem>
            </Nav>
        </Navbar>
    );
}